<template>
  <div class="salesTrends chartPage" @click="closeByDialog">
    <AppHeader>
      <p slot="center">销售走势</p>
    </AppHeader>

    <van-tabs v-model="active" animated sticky :offset-top="offsetTop" @click="tabOnClick">
      <van-tab title="销售额">
        <div class="contrast">
          <van-tabs v-model="salesActive" @click="salesTabClick">
            <van-tab title="公历同期"></van-tab>
            <van-tab title="农历同期"></van-tab>
          </van-tabs>
        </div>

        <div class="filters">
          <div class="date-filters">
            <input type="text" @click.stop="openByDialog" :value="calendar.display" readonly />
          </div>
          <transition name="fade">
            <div class="calendar-dialog" v-if="calendar.show">
              <div class="calendar-dialog-mask" @click="closeByDialog"></div>
              <div class="calendar-dialog-body">
                <calendar
                  :range="calendar.range"
                  :zero="calendar.zero"
                  :lunar="calendar.lunar"
                  :value="calendar.value"
                  @select="calendar.select"
                ></calendar>
              </div>
            </div>
          </transition>
        </div>

        <div class="myChart-box">
          <div class="myChart" ref="myChart_sku" style="width: 375px;height: 360px"></div>
        </div>

        <div class="chart-text">
          <p>销售总额: {{ totalSum }}</p>
          <p>同期销售总额: {{ tqTotalSum }}</p>
          <p>销售额增幅率: {{ increaseRate }}</p>
        </div>
      </van-tab>
      <van-tab title="毛利额">
        <div class="contrast">
          <van-tabs v-model="profitActive" @click="profitTabClick">
            <van-tab title="公历同期"></van-tab>
            <van-tab title="农历同期"></van-tab>
          </van-tabs>
        </div>

        <div class="filters">
          <div class="date-filters">
            <input type="text" @click.stop="openByDialog" :value="calendar2.display" readonly />
          </div>
          <transition name="fade">
            <div class="calendar-dialog" v-if="calendar2.show">
              <div class="calendar-dialog-mask" @click="closeByDialog"></div>
              <div class="calendar-dialog-body">
                <calendar
                  :range="calendar2.range"
                  :zero="calendar2.zero"
                  :lunar="calendar2.lunar"
                  :value="calendar2.value"
                  @select="calendar2.select"
                ></calendar>
              </div>
            </div>
          </transition>
        </div>

        <div class="myChart-box">
          <div class="myChart" ref="myChart_jjxSku" style="width: 375px;height: 360px"></div>
        </div>

        <div class="chart-text">
          <p>毛利总额: {{ totalSum }}</p>
          <p>同期毛利总额: {{ tqTotalSum }}</p>
          <p>毛利额增幅率: {{ increaseRate }}</p>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import Vue from "vue";
import { Tab, Tabs, Calendar, Field } from "vant";

Vue.use(Tab)
  .use(Tabs)
  .use(Calendar)
  .use(Field);

import AppHeader from "@/components/AppHeader.vue";
import calendar from "@/components/calendar/calendar.vue";

import echarts from "echarts/lib/echarts"; // 引入基本模板
import line from "echarts/lib/chart/line"; // 引入线状图组件
import legend from "echarts/lib/component/legend"; // 引入legend组件
import tooltip from "echarts/lib/component/tooltip"; // 引入tooltip组件

export default {
  name: "SalesTrends",
  components: {
    AppHeader,
    calendar
  },

  data() {
    return {
      active: 0,
      salesActive: 0,
      profitActive: 0,
      screenWidth: document.body.clientWidth, // 屏幕尺寸
      offsetTop: 90 / (750 / document.body.clientWidth),

      calendar: {
        display: "2020/02/16 ~ 2020/03/16", //输入框站位日期
        show: false, //是否显示
        range: true,
        zero: true,
        value: [
          [2018, 2, 16],
          [2019, 2, 16]
        ], //默认选中日期
        lunar: true, //显示农历
        select: (begin, end) => {
          // console.log(begin, end);
          this.calendar_startDate = begin.join("-");
          this.calendar_endDate = end.join("-");
          this.calendar.show = false;
          this.calendar.value = [begin, end];
          this.calendar.display = begin.join("/") + " ~ " + end.join("/");
          this.$nextTick(() => {
            this.get_salesChart();
          });
        }
      },
      calendar2: {
        display: "2020/02/16 ~ 2020/03/16", //输入框站位日期
        show: false, //是否显示
        range: true,
        zero: true,
        value: [
          [2018, 2, 16],
          [2019, 2, 16]
        ], //默认选中日期
        lunar: true, //显示农历
        select: (begin, end) => {
          // console.log(begin, end);
          this.calendar2_startDate = begin.join("-");
          this.calendar2_endDate = end.join("-");
          this.calendar2.show = false;
          this.calendar2.value = [begin, end];
          this.calendar2.display = begin.join("/") + " ~ " + end.join("/");
          this.$nextTick(() => {
            this.get_profitChart();
          });
        }
      },
      calendar_startDate: "",
      calendar_endDate: "",
      calendar2_startDate: "",
      calendar2_endDate: "",
      getStoreInfo: "",
      sku_myChart: null,
      resultData: [],
      totalSum: 0,
      tqTotalSum: 0,
      increaseRate: 0,
      //销售额
      theDay_xse: [],
      contemporary_tqxse: [],
      contemporary_nlxse: [],
      xse_all: 0,
      tqxse_all: 0,
      nlxse_all: 0
    };
  },
  created() {
    this.get_defaultData();
    this.get_data();
  },
  mounted() {},
  methods: {
    tabOnClick(name, title, index) {
      if (name == 0) {
        if (this.salesActive == 0) {
          this.$nextTick(() => {
            this.drawLine_xse();
            this.totalSum = this.xse_all;
            this.tqTotalSum = this.tqxse_all;
          });
        } else if (this.salesActive == 1) {
          this.$nextTick(() => {
            this.drawLine_nlxse();
            this.totalSum = this.xse_all;
            this.tqTotalSum = this.nlxse_all;
          });
        }
      } else if (name == 1) {
        if (this.profitActive == 0) {
          this.$nextTick(() => {
            this.drawLine_mle();
            this.totalSum = this.mle_all;
            this.tqTotalSum = this.tqmle_all;
          });
        } else if (this.profitActive == 1) {
          this.$nextTick(() => {
            this.drawLine_nlmle();
            this.totalSum = this.mle_all;
            this.tqTotalSum = this.nlmle_all;
          });
        }
      }
    },
    salesTabClick(name, title, index) {
      if (name == 0) {
        this.$nextTick(() => {
          this.drawLine_xse();
          this.totalSum = this.xse_all;
          this.tqTotalSum = this.tqxse_all;
        });
      } else if (name == 1) {
        this.$nextTick(() => {
          this.drawLine_nlxse();
          this.totalSum = this.xse_all;
          this.tqTotalSum = this.nlxse_all;
        });
      }
    },
    profitTabClick(name, title, index) {
      if (name == 0) {
        this.$nextTick(() => {
          this.drawLine_mle();
          this.totalSum = this.mle_all;
          this.tqTotalSum = this.tqmle_all;
        });
      } else if (name == 1) {
        this.$nextTick(() => {
          this.drawLine_nlmle();
          this.totalSum = this.mle_all;
          this.tqTotalSum = this.nlmle_all;
        });
      }
    },
    get_defaultData() {
      this.getStoreInfo = this.$store.getters.getStoreInfo; //获取当前选择的门店信息
      // console.log(this.$store.state.defaultDate);
      const time1 = this.formatDate.pastDate(
        this.$store.state.defaultDate + 1,
        "-"
      );
      const time2 = this.formatDate.pastDate(2, "-");
      this.calendar_startDate = time1; //日历默认开始时间
      this.calendar_endDate = time2; //日历默认结束时间
      this.calendar.value = [time1.split("-"), time2.split("-")]; //日历默认选中日期
      //日历-输入框站位日期
      this.calendar.display =
        this.formatDate.pastDate(this.$store.state.defaultDate + 1, "-") +
        " ~ " +
        this.formatDate.pastDate(2, "-");

      this.calendar2_startDate = time1; //日历默认开始时间
      this.calendar2_endDate = time2; //日历默认结束时间
      this.calendar2.value = [time1.split("-"), time2.split("-")]; //日历默认选中日期
      //日历-输入框站位日期
      this.calendar2.display =
        this.formatDate.pastDate(this.$store.state.defaultDate + 1, "-") +
        " ~ " +
        this.formatDate.pastDate(2, "-");
    },
    // 获取数据
    get_data() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.dateArray = this.dateArray2 = this.formatDate.dateArray(
        this.calendar_startDate,
        this.calendar_endDate
      );
      let params = {
        storeId: this.getStoreInfo.gid,
        startDate: this.calendar_startDate,
        endDate: this.calendar_endDate
      };
      this.serverHost
        .fetchPost("/getReportData", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          const resultData = res.data.data;
          //销售额
          const theDay_xse = [];
          const contemporary_tqxse = [];
          const contemporary_nlxse = [];

          //毛利额
          const theDay_mle = [];
          const contemporary_tqmle = [];
          const contemporary_nlmle = [];
          resultData.forEach(item => {
            //销售额
            if (item.salesVolume) {
              theDay_xse.push(item.salesVolume);
            } else {
              theDay_xse.push("0");
            }
            if (item.salesVolumeSame) {
              contemporary_tqxse.push(item.salesVolumeSame);
            } else {
              contemporary_tqxse.push("0");
            }
            if (item.nsalesVolumeSame) {
              contemporary_nlxse.push(item.nsalesVolumeSame);
            } else {
              contemporary_nlxse.push("0");
            }

            //毛利额
            if (item.grossMargin) {
              theDay_mle.push(item.grossMargin);
            } else {
              theDay_mle.push("0");
            }
            if (item.grossMarginSame) {
              contemporary_tqmle.push(item.grossMarginSame);
            } else {
              contemporary_tqmle.push("0");
            }
            if (item.ngrossMarginSame) {
              contemporary_nlmle.push(item.ngrossMarginSame);
            } else {
              contemporary_nlmle.push("0");
            }
          });
          this.resultData = resultData;
          //销售额
          this.theDay_xse = theDay_xse;
          this.contemporary_tqxse = contemporary_tqxse;
          this.contemporary_nlxse = contemporary_nlxse;
          var xse_all = 0;
          var tqxse_all = 0;
          var nlxse_all = 0;
          theDay_xse.forEach(item => {
            xse_all += parseFloat(item);
          });
          contemporary_tqxse.forEach(item => {
            tqxse_all += parseFloat(item);
          });
          contemporary_nlxse.forEach(item => {
            nlxse_all += parseFloat(item);
          });
          this.xse_all = xse_all.toFixed(2);
          this.tqxse_all = tqxse_all.toFixed(2);
          this.nlxse_all = nlxse_all.toFixed(2);
          this.totalSum = this.xse_all;
          this.tqTotalSum = this.tqxse_all;
          this.drawLine_xse();

          //毛利额
          this.theDay_mle = theDay_mle;
          this.contemporary_tqmle = contemporary_tqmle;
          this.contemporary_nlmle = contemporary_nlmle;
          var mle_all = 0;
          var tqmle_all = 0;
          var nlmle_all = 0;
          theDay_mle.forEach(item => {
            mle_all += parseFloat(item);
          });
          contemporary_tqmle.forEach(item => {
            tqmle_all += parseFloat(item);
          });
          contemporary_nlmle.forEach(item => {
            nlmle_all += parseFloat(item);
          });
          this.mle_all = mle_all.toFixed(2);
          this.tqmle_all = tqmle_all.toFixed(2);
          this.nlmle_all = nlmle_all.toFixed(2);
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    },
    get_salesChart() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.dateArray = this.formatDate.dateArray(
        this.calendar_startDate,
        this.calendar_endDate
      );
      let params = {
        storeId: this.getStoreInfo.gid,
        startDate: this.calendar_startDate,
        endDate: this.calendar_endDate
      };
      this.serverHost
        .fetchPost("/getReportData", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          const resultData = res.data.data;
          //销售额
          const theDay_xse = [];
          const contemporary_tqxse = [];
          const contemporary_nlxse = [];
          resultData.forEach(item => {
            //销售额
            if (item.salesVolume) {
              theDay_xse.push(item.salesVolume);
            } else {
              theDay_xse.push("0");
            }
            if (item.salesVolumeSame) {
              contemporary_tqxse.push(item.salesVolumeSame);
            } else {
              contemporary_tqxse.push("0");
            }
            if (item.nsalesVolumeSame) {
              contemporary_nlxse.push(item.nsalesVolumeSame);
            } else {
              contemporary_nlxse.push("0");
            }
          });
          this.resultData = resultData;
          //销售额
          this.theDay_xse = theDay_xse;
          this.contemporary_tqxse = contemporary_tqxse;
          this.contemporary_nlxse = contemporary_nlxse;
          var xse_all = 0;
          var tqxse_all = 0;
          var nlxse_all = 0;
          theDay_xse.forEach(item => {
            xse_all += parseFloat(item);
          });
          contemporary_tqxse.forEach(item => {
            tqxse_all += parseFloat(item);
          });
          contemporary_nlxse.forEach(item => {
            nlxse_all += parseFloat(item);
          });
          this.xse_all = xse_all.toFixed(2);
          this.tqxse_all = tqxse_all.toFixed(2);
          this.nlxse_all = nlxse_all.toFixed(2);
          if (this.salesActive == 0) {
            this.$nextTick(() => {
              this.drawLine_xse();
              this.totalSum = this.xse_all;
              this.tqTotalSum = this.tqxse_all;
            });
          } else {
            this.$nextTick(() => {
              this.drawLine_nlxse();
              this.totalSum = this.xse_all;
              this.tqTotalSum = this.nlxse_all;
            });
          }
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    },
    get_profitChart() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.dateArray2 = this.formatDate.dateArray(
        this.calendar2_startDate,
        this.calendar2_endDate
      );
      let params = {
        storeId: this.getStoreInfo.gid,
        startDate: this.calendar2_startDate,
        endDate: this.calendar2_endDate
      };
      this.serverHost
        .fetchPost("/getReportData", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          const resultData = res.data.data;
          //毛利额
          const theDay_mle = [];
          const contemporary_tqmle = [];
          const contemporary_nlmle = [];
          resultData.forEach(item => {
            //毛利额
            if (item.grossMargin) {
              theDay_mle.push(item.grossMargin);
            } else {
              theDay_mle.push("0");
            }
            if (item.grossMarginSame) {
              contemporary_tqmle.push(item.grossMarginSame);
            } else {
              contemporary_tqmle.push("0");
            }
            if (item.ngrossMarginSame) {
              contemporary_nlmle.push(item.ngrossMarginSame);
            } else {
              contemporary_nlmle.push("0");
            }
          });
          this.resultData = resultData;

          //毛利额
          this.theDay_mle = theDay_mle;
          this.contemporary_tqmle = contemporary_tqmle;
          this.contemporary_nlmle = contemporary_nlmle;
          var mle_all = 0;
          var tqmle_all = 0;
          var nlmle_all = 0;
          theDay_mle.forEach(item => {
            mle_all += parseFloat(item);
          });
          contemporary_tqmle.forEach(item => {
            tqmle_all += parseFloat(item);
          });
          contemporary_nlmle.forEach(item => {
            nlmle_all += parseFloat(item);
          });
          this.mle_all = mle_all.toFixed(2);
          this.tqmle_all = tqmle_all.toFixed(2);
          this.nlmle_all = nlmle_all.toFixed(2);
          if (this.profitActive == 0) {
            this.$nextTick(() => {
              this.drawLine_mle();
              this.totalSum = this.mle_all;
              this.tqTotalSum = this.tqmle_all;
            });
          } else {
            this.$nextTick(() => {
              this.drawLine_nlmle();
              this.totalSum = this.mle_all;
              this.tqTotalSum = this.nlmle_all;
            });
          }
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    },
    openByDialog() {
      this.calendar.show = true;
      this.calendar2.show = true;
    },
    closeByDialog() {
      this.calendar.show = false;
      this.calendar2.show = false;
    },
    //画图
    drawLine_xse() {
      // 基于准备好的dom，初始化echarts实例
      let sku_myChart = echarts.init(this.$refs.myChart_sku);
      let dateArray = this.dateArray;
      let theDay = this.theDay_xse;
      let contemporary = this.contemporary_tqxse;
      // 绘制图表
      sku_myChart.setOption({
        //鼠标移入图标提示层
        tooltip: {
          trigger: "axis"
        },
        //图例组件相关的行为，必须引入图例组件后才能使用。
        legend: {
          data: ["销售额", "同期销售额"],
          bottom: 0,
          left: "center"
        },
        //图标位置
        grid: {
          top: "5%",
          left: "3%",
          right: "12%",
          bottom: "10%",
          containLabel: true
        },
        //图标 x 轴
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: dateArray
        },
        //图标 y 轴
        yAxis: {
          type: "value"
        },
        //数据
        series: [
          {
            name: "销售额",
            type: "line",
            data: theDay
          },
          {
            name: "同期销售额",
            type: "line",
            data: contemporary
          }
        ]
      });
    },
    drawLine_nlxse() {
      // 基于准备好的dom，初始化echarts实例
      let sku_myChart = echarts.init(this.$refs.myChart_sku);
      let dateArray = this.dateArray;
      let theDay = this.theDay_xse;
      let contemporary = this.contemporary_nlxse;
      // 绘制图表
      sku_myChart.setOption({
        //鼠标移入图标提示层
        tooltip: {
          trigger: "axis"
        },
        //图例组件相关的行为，必须引入图例组件后才能使用。
        legend: {
          data: ["销售额", "同期销售额"],
          bottom: 0,
          left: "center"
        },
        //图标位置
        grid: {
          top: "5%",
          left: "3%",
          right: "12%",
          bottom: "10%",
          containLabel: true
        },
        //图标 x 轴
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: dateArray
        },
        //图标 y 轴
        yAxis: {
          type: "value"
        },
        //数据
        series: [
          {
            name: "销售额",
            type: "line",
            data: theDay
          },
          {
            name: "同期销售额",
            type: "line",
            data: contemporary
          }
        ]
      });
    },
    drawLine_mle() {
      // 基于准备好的dom，初始化echarts实例
      let sku_myChart = echarts.init(this.$refs.myChart_jjxSku);
      let dateArray2 = this.dateArray2;
      let theDay = this.theDay_mle;
      let contemporary = this.contemporary_tqmle;
      // 绘制图表
      sku_myChart.setOption({
        //鼠标移入图标提示层
        tooltip: {
          trigger: "axis"
        },
        //图例组件相关的行为，必须引入图例组件后才能使用。
        legend: {
          data: ["毛利额", "同期毛利额"],
          bottom: 0,
          left: "center"
        },
        //图标位置
        grid: {
          top: "5%",
          left: "3%",
          right: "12%",
          bottom: "10%",
          containLabel: true
        },
        //图标 x 轴
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: dateArray2
        },
        //图标 y 轴
        yAxis: {
          type: "value"
        },
        //数据
        series: [
          {
            name: "毛利额",
            type: "line",
            data: theDay
          },
          {
            name: "同期毛利额",
            type: "line",
            data: contemporary
          }
        ]
      });
    },
    drawLine_nlmle() {
      // 基于准备好的dom，初始化echarts实例
      let sku_myChart = echarts.init(this.$refs.myChart_jjxSku);
      let dateArray2 = this.dateArray2;
      let theDay = this.theDay_mle;
      let contemporary = this.contemporary_nlmle;
      // 绘制图表
      sku_myChart.setOption({
        //鼠标移入图标提示层
        tooltip: {
          trigger: "axis"
        },
        //图例组件相关的行为，必须引入图例组件后才能使用。
        legend: {
          data: ["毛利额", "同期毛利额"],
          bottom: 0,
          left: "center"
        },
        //图标位置
        grid: {
          top: "5%",
          left: "3%",
          right: "12%",
          bottom: "10%",
          containLabel: true
        },
        //图标 x 轴
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: dateArray2
        },
        //图标 y 轴
        yAxis: {
          type: "value"
        },
        //数据
        series: [
          {
            name: "毛利额",
            type: "line",
            data: theDay
          },
          {
